@charset "UTF-8";
header {
    width:100%; height:0; min-width:1280px; position:absolute; z-index:10;
    @include boxSizing;
    &.active {
        height:120px; position:fixed; background-color:$navy;
        .hd_wrap {
             #logo {
                top:30px;
                a {
                    &:nth-of-type(1) { display:none; }
                    &:nth-of-type(2) { display:block; }
                }
            }
        }
        #gnbBtn { top:29px; }
        .generation { padding:36px 0 0; }
    }
    .hd_wrap {
        max-width:1580px; min-width:1180px; margin:0 auto; position:relative; padding:0 50px;
        #logo {
            position:absolute; left:50%; top:50px; opacity:0;
            @include transform(translate(-50%,0));
            a {
                &:nth-of-type(1) { display:block; }
                &:nth-of-type(2) { display:none; }
            }
        }
    }
    #gnbBtn {
        width:125px; position:absolute; left:32px; top:64px; z-index:10; padding:20px; opacity:0;
        &:hover {
            i { &:after, &:before { width:20px; } }
        }
        &.active {
            .btn_txt { color:#d2ad83; }
            i {
                background-color:#d2ad83;
                &:after, &:before { width:20px; background-color:#d2ad83; }
            }
        }
        .btn_txt {
            position:relative; left:28px;
            @include transition(all, .2s);
        }
        span { display:block; color:$white; font-size:20px; }
        i {
            width:30px; height:2px; display:block; background-color:$white; position:relative; top:9px; left:0;
            &:after, &:before {
                width:30px; height:2px; content:""; display:block; background-color:$white; position:absolute;
                @include transition(all, .2s);
            }
            &:after { top:10px; }
            &:before { top:-10px; }
        }
    }
    .generation {
        position:absolute; right:50px; top:0; padding:70px 0 0; display:flex; align-items:center;
        li {
            color:$white; font-size:16px; opacity:0;
            &.hd_phone { margin:0 60px 0 0; font-family:$Play; }
            a { color:$white; }
            .household_link {
                display:block;
                width:180px; padding:15px 0; text-align:center; background-color:#a8835b; color:$white; font-size:16px;
                @include transform(skew(-0.03deg));
                @include borderRadius(30px);
                @include transition(all, .3s);
                &:hover { box-shadow: 0 0 20px 20px #8d6438 inset; }
            }
        }
    }
    .menu_wrap {width:0; height:100vh;position:relative;overflow:hidden;}
    #gnbPopup {
        height:100vh; position:absolute; top:0; background-color:$navy;
        z-index:9; left:0;
        @include boxSizing;
        .gnb_wrap {
            padding:250px 450px 0 160px;
            li {
                width:fit-content; position:relative;
                a {
                    width:600px; height:100%; display:block; padding:17px 0;
                    &:hover {
                        p { color:#d2ad83; @include transform(translate(10px,0)); }
                    }
                }
                p {
                    display:inline-block; font-size:42px; color:$white;
                    @include transition(all, .2s);
                }
                span { color:#616366; font-size:14px; position:relative; left:20px; top:6px; }
            }
        }
        .address {
            padding:150px 0 0 160px; display:flex; align-items:center;
            ul { padding:0 0 0 70px; }
            li {
                padding:8px 0 0; color:#878a8d; font-size:14px;
                @include transform(skew(-0.03deg));
                &:first-child { padding:0; }
            }
        }
        .close_btn {
            width:180px; height:180px; position:absolute; top:50%; right:-90px;
            display:block; border:1px solid $gold; background-color:rgba(5, 5, 5, .2);
            @include transform(translate(0,-50%));
            @include borderRadius(100px);
            @include transition(all, .2s);
            &:hover {
                box-shadow: 0 0 100px 100px $gold inset;
                &:after {@include transform(translate(-50%,-50%) rotate(90deg));}
            }
            &:after {
                width:20px; height:20px; content:""; display:block; position:absolute; left:50%; top:50%;
                background:url(../../images/common/x_icon.png) 0 0 no-repeat;
                @include transform(translate(-50%,-50%));
                @include transition(all, .2s);
            }
        }
    }
    .dim {
        width:100%; height:100vh; position:absolute; left:0; top:0;
        background-color:rgba(0, 0, 0, .5); display:none; opacity:0;
    }
}

footer {
    width:100%; min-width:1280px; background-color:$white; padding:50px 70px 65px;
    @include boxSizing;
    ul {
        display:flex; position:relative;
        align-items:center;
        li {
            color:#514540; font-size:14px;
            &:nth-of-type(2) { padding:0 0 0 70px;display:flex;flex-direction:column;justify-content:center;}
            &:nth-of-type(3) {position:absolute; right:0; top:25px;color:#a19894;}
        }
        span {
            padding:9px 0 0;
            display:block; @include transform(skew(-0.03deg));
            &:first-child { padding:0; }
        }
    }
}








































