@charset "UTF-8";

/* Font style setting */
$NanumSquare:"NanumSquare", Arial, Apple SD Gothic Neo, sans-serif;
$Play:"Play", Arial, Apple SD Gothic Neo, sans-serif;
$Centaur:"Centaur", Arial, Apple SD Gothic Neo, sans-serif;

/* Point color setting */
$black:#000;
$white:#fff;

$red:#a50034;
$navy:#191d24;
$gold:#a8835b;
$goldYellow:#d2ad83;
$brown:#514540;


$gray1:#242526;
$gray2:#646566;
$gray3:#a4a4a4;
$gray4:#ddd;
$gray5:#f4f5f6;

/* Background - color setting */
$bg-black:$black;
$bg-gray1:$gray1;
$bg-gray2:$gray2;
$bg-gray3:$gray3;
$bg-gray4:$gray4;
$bg-gray5:$gray5;

/* Border - color setting */
$bd-black:$black;
$bd-gray1:$gray1;
$bd-gray2:$gray2;
$bd-gray3:$gray3;
$bd-gray4:$gray4;
$bd-gray5:$gray5;

/* Font - color setting */
$txt-black:$black;
$txt-gray1:$gray1;
$txt-gray2:$gray2;
$txt-gray3:$gray3;
$txt-gray4:$gray4;
$txt-gray5:$gray5;

/* Input - color setting */
$inp-bd-base:$gray1;

/* Button - color setting */
$btn-black:$black;

/**
 * BASE SETTING
 */
$base-font:$NanumSquare;
$base-font-size:20px;
$base-font-color:$black;

$letter-spacing:0;

$light:300;
$regular:500;
$bold:600;
$extraBold:800;
